    padding: 0px 4px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    &:hover {
        cursor: pointer;
    }

    &.selected {
        background-color: #9BB3DA;

        &:hover {
            background-color: #9BB3DA;
        }
    }
}
table td {
    padding: 0 !important;
    border: 1px solid #a9a9a9;

    // .cell:hover {
        // background-color: #f9f9f9;
    // }
}
table th {
    padding: 0 !important;
    border: 1px solid #a9a9a9;
    background-color: #eaeaea;

    &:hover {
        background-color: #e3e3e3;
        cursor: pointer;
    }
}
